.title {
  font-family: "FuturaPT", Arial, Helvetica, sans-serif;
  font-weight: 500;
  font-size: 22px;
  line-height: 123%;
  color: $color-181818;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }

  &_sm {
    font-weight: 450;
    font-size: 16px;
  }

  &_big {
    font-size: 45px;
    font-weight: 600;
  }
}
