* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}

.container {
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 1040px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: -10px;
}

.col {
  width: 100%;
  padding: 10px;

  // &_1 {
  //   width: 8.333333%;
  // }

  &_2 {
    width: 16.666667%;
  }

  &_3 {
    width: 25%;
  }

  &_4 {
    width: 33.333333%;
  }

  &_5 {
    width: 41.666667%;

    @include tablet {
      width: 100%;
    }
  }

  &_6 {
    width: 50%;
  }

  &_7 {
    width: 58.333333%;

    @include tablet {
      width: 100%;
    }
  }

  &_8 {
    width: 66.666667%;
  }

  &_10 {
    width: 83.3333%;
  }
}
