body {
  font-family: "FuturaPT", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 135%;
  // color: $color-333333;
  color: $color-7E8791;
  background: $color-F5F5F7;
}

.main {
  padding-top: 80px;
  padding-bottom: 80px;

  &__items {
    display: flex;
    flex-direction: column;
    background: $color-FFFFFF;
    box-shadow: $shadow-1;
  }
}

.sidebar {
  background: $color-181818;
  height: 100%;

  &__img {
    display: flex;

    img {
      width: 100%;
      height: auto;
    }
  }
}

